.menu-item {
  min-width: 30%;
  height: 240px;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  flex: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  .backgroud-image {
    width: 100%;
    height: 100%;
    background: center;
    background-size: cover;
  }

  &:hover {
    cursor: pointer;
    & .backgroud-image {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
    & .content {
      opacity: 0.9;
    }
  }

  &.large {
    height: 380px;
  }

  .content {
    height: 90px;
    padding: 0 25px;
    border: 1px solid black;
    text-align: center;
    background-color: white;
    opacity: 0.7;
    position: absolute;

    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }
    .subtitle {
      font-weight: lighter;
      font-size: 16px;
    }
  }
}
